import React, { useState, useEffect } from "react";
import axios from "axios";
import { Parser } from "html-to-react";
import Navbar from "../Navbar";
import SearchAllBlog from "../SearchAllBlog";
import Footer from "../Footer";
import Enquire from "../Enquire";
import { HiOutlineCheck, HiOutlineClock } from "react-icons/hi";

import { Link } from "react-router-dom";

function AllPackage() {
  // State to store blog posts fetched from the API
  const [blogPosts, setBlogPosts] = useState([]);

  // State to manage pagination
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 3; // Number of blog posts to display per page

  // Max length for content preview
  const MAX_LENGTH = 250;

  // State to store search results
  const [searchResults, setSearchResults] = useState(null);

  // Function to fetch blog posts from the API using Axios
  useEffect(() => {
    axios
      .get("http://localhost/himalayan/API_fetch_all_blog.php")
      .then((response) => {
        setBlogPosts(response.data);
      })
      .catch((error) => {
        console.error("Error fetching blog data:", error);
      });
  }, []);

  // Calculate the index of the first and last items to display on the current page
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentBlogPosts = blogPosts.slice(indexOfFirstItem, indexOfLastItem);

  // Function to handle page change
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  // Function to handle search
  const handleSearch = (searchTerm) => {
    if (!searchTerm) {
      setSearchResults(null);
      return;
    }

    const filteredPosts = blogPosts.filter((post) =>
      post.BName.toLowerCase().includes(searchTerm.toLowerCase())
    );
    setSearchResults(filteredPosts);
  };

  // Choose which blog posts to display based on search results
  const displayBlogPosts = searchResults || currentBlogPosts;

  const parser = new Parser();
  return (
    <>
      {/* Navbar and Hero Section */}
      <div className="relative">
        <section className="bg-[url('https://images.thrillophilia.com/image/upload/s--_XQ_pqQH--/c_fill,g_auto,h_642,w_1400/dpr_1.0/v1/collections/images/015/120/655/original/1655472205_shutterstock_1141239563.jpg.jpg')] w-full h-96 bg-cover bg-center ">
          <Navbar />
          <h2 className="absolute text-3xl text-white bottom-0 left-0 -translate-x-1/2 border-l-4 border-primary font-semibold pl-4">
          Best Things to Do Across The Globe
          </h2>
        </section>
      </div>

      {/* Blog Posts Section */}

      <div class="flex">
        <div class="flex-1">
          <section className="text-gray-600 body-font">
            <div className="container px-10 py-24 mx-auto">
              {/* SearchAllBlog */}

              <div className="flex flex-col items-center justify-center mb-8 md:flex-row md:justify-between md:mb-10">
                <h1 className="text-4xl font-bold text-center mb-2 md:mb-0">
                Best Things to Do Across The Globe
                </h1>
                <div className="md:ml-4">
                  <div className="form-control w-full max-w-xs">
                    <SearchAllBlog handleSearch={handleSearch} />
                  </div>
                </div>
              </div>
              {/* Display the blog posts */}
              <div className="flex-wrap -m-4 grid md:grid-cols-1 md:gap-3">
                {displayBlogPosts.map((post) => (
                  <div key={post.BID} className="md:w-full">
                    <div className="p-4">
                      <div className="card w-auto bg-base-100 shadow-xl h-full border-2  overflow-hidden">
                        <img
                          className="lg:h-96 md:h-96 w-full object-cover object-center"
                          src={post.image}
                          alt={post.BAlt}
                        />

                        <div className="p-6 border-l-4  border-l-secondary">
                          <h1 className="title-font font-semibold text-2xl  text-gray-900 mb-3">
                            {post.BName}
                          </h1>
                          {/* Display content preview */}

                          <div className="flex items-center justify-between ">
                            <div className="flex items-center text-primary hover:text-secondary ">
                              <HiOutlineClock className="h-8 w-8 m-2" />
                              <div className="text-xl md:mb-2 lg:mb-0">
                                5D/4N
                              </div>
                            </div>
                            <div className="flex items-center">
                              <div className="text-xl md:mb-2 lg:mb-0">
                              
                                <span className="text-2xl font-bold	 text-black-500">₹  12950.00</span>
                                
                                <span className="text-gray-500">  per adult</span>
                              </div>
                            </div>  

                            <button className="text-white bg-primary border-0 py-2 px-6 focus:outline-none hover:bg-secondary rounded-xl text-lg">
                              Learn More 
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              {/* Pagination */}
              <div className="flex justify-center mt-4">
                {/* Previous Button */}
                <button
                  onClick={() => handlePageChange(currentPage - 1)}
                  disabled={currentPage === 1}
                  className="mx-1 px-3 py-1 border rounded-xl bg-white text-primary"
                >
                  Previous
                </button>

                {/* Pagination buttons */}
                {Array.from({
                  length: Math.ceil(blogPosts.length / itemsPerPage),
                }).map((_, index) => (
                  <button
                    key={index}
                    onClick={() => handlePageChange(index + 1)}
                    className={`mx-1 px-3 py-1 border rounded-xl ${
                      currentPage === index + 1
                        ? "bg-primary text-white"
                        : "bg-white text-primary"
                    }`}
                  >
                    {index + 1}
                  </button>
                ))}

                {/* Next Button */}
                <button
                  onClick={() => handlePageChange(currentPage + 1)}
                  disabled={
                    currentPage === Math.ceil(blogPosts.length / itemsPerPage)
                  }
                  className="mx-1 px-3 py-1 border rounded-xl bg-white text-primary"
                >
                  Next
                </button>
              </div>
            </div>
          </section>
        </div>


      {/* side bar code */}
        <div class="hidden md:flex sticky top-0 h-screen w-1/4 md:mt-5 md:mr-10 md:mb-20">
         <Enquire/>
        </div>
         {/* side bar code */}
      </div>

      <Footer />
    </>
  );
}

export default AllPackage;
